<template>
  <div class="container-fluid">
    <div class="row justify-content-center full">
      <div class="col-2 text-center align-self-center">
        <button class="btn-play" @click="Action">
          <img
            src="../assets/play.svg"
            width="74px"
            height="74px"
            v-if="play == false"
          />
          <img src="../assets/pause.svg" width="74px" height="74px" v-else />
        </button>
      </div>
      <div class="col mt-5">
        <vue-slider
          :data="dates"
          :order="false"
          :tooltip="'none'"
          v-model="selectedIndex"
          @change="UpdateTime"
          :marks="marks"
        >
          <template v-slot:step="{ value, label }">
            <div
              class="mark_videoclip"
              :style="{
                background:
                  tooltips[tooltips.findIndex((e) => e.value == label)].color,
                color:
                  tooltips[tooltips.findIndex((e) => e.value == label)].text,
              }"
            >
              {{ Formater(value) }}
            </div>
            <div
              class="mark_videoclip_dot"
              :style="{
                background:
                  tooltips[tooltips.findIndex((e) => e.value == label)].color,
              }"
            ></div>
          </template>
        </vue-slider>
      </div>
      <div class="col-2 text-center align-self-center">
        <Modal @update-indicator="UpdateIndicator" />
      </div>
    </div>
  </div>
</template>

<script>
import { DateToText } from "../scripts/Download";
import Modal from "../components/Modal";
export default {
  components: {
    Modal,
  },
  props: ["dates", "tooltips"],
  data() {
    return {
      selectedIndex: 0,
      min: 0,
      max: 7,
      text: "Play",
      play: false,
      t: [],
    };
  },
  methods: {
    UpdateTime() {
      this.$emit("update-time", this.selectedIndex);
    },
    UpdateIndicator(type) {
      this.$emit("update-indicator", type);
    },
    Play() {
      const step = 2000;
      this.text = "Pause";
      this.selectedIndex = this.dates[0];
      this.UpdateTime();
      for (let i = 1; i < this.max; i++) {
        this.t.push(
          setTimeout(() => {
            if (this.play) {
              this.selectedIndex = this.dates[i];
              this.UpdateTime();
            }
            if (i == this.max - 1) {
              this.Pause();
            }
          }, (i) * step)
        );
      }
    },
    Pause() {
      this.play = false;
      this.t.forEach((e) => {
        clearTimeout(e);
      });
      this.text = "Play";
    },
    Action() {
      this.play = !this.play;
      if (this.play) {
        this.Play();
      } else {
        this.Pause();
      }
    },
    Formater(date) {
      return DateToText(date);
    },
  },
  computed: {
    marks: function () {
      const res = {};
      this.dates.forEach((date, i) => {
        res[date] = {
          label: `${this.tooltips[i].value}`,
          labelStyle: {
            backgroundColor: this.tooltips[i].color,
            "border-radius": "40px",
            border: "2px solid white",
            "margin-top": "15px",
            padding: "10px",
            color: this.tooltips[i].text,
            "font-size": "20px",
            "font-weight": "bold",
          },
        };
      });
      return res;
    },
  },
};
</script>

