import proj4 from "proj4";
proj4.defs("EPSG:3857");
proj4.defs("EPSG:4326");

const PM = [0, 12, 35.4, 55.4, 150, 200, 250, 1000];
const AQI = [0, 50, 100, 150, 200, 300, 500, 1000];
const PDK = [0, 1, 10];
const CODES = {
    PM: 348,
    AQI: 355,
    PDK: 400
};
const RANGES = {
    PM,
    AQI,
    PDK
}
const CONVERSION = {
    PM: 1000,
    AQI: 1,
    PDK: 1
};

const COLORS_PM = [
    { color: 'rgb(0,153,102)', text: "rgb(250,250,250)" },
    { color: 'rgb(255,222,51)', text: "rgb(0,0,0)" },
    { color: 'rgb(255,153,51)', text: "rgb(250,250,250)" },
    { color: 'rgb(204,0,51)', text: "rgb(250,250,250)" },
    { color: 'rgb(102,0,153)', text: "rgb(250,250,250)" },
    { color: 'rgb(126,0,35)', text: "rgb(250,250,250)" },
    { color: 'rgb(0,0,0)', text: "rgb(250,250,250)" }];

const COLORS_AQI = [
    { color: 'rgb(0,153,102)', text: "rgb(250,250,250)" },
    { color: 'rgb(255,222,51)', text: "rgb(0,0,0)" },
    { color: 'rgb(255,153,51)', text: "rgb(250,250,250)" },
    { color: 'rgb(204,0,51)', text: "rgb(250,250,250)" },
    { color: 'rgb(102,0,153)', text: "rgb(250,250,250)" },
    { color: 'rgb(126,0,35)', text: "rgb(250,250,250)" },
    { color: 'rgb(0,0,0)', text: "rgb(250,250,250)" }];

const COLORS_PDK = [
    { color: 'rgb(0,153,102)', text: "rgb(250,250,250)" },
    { color: 'rgb(204,0,51)', text: "rgb(250,250,250)" }
];

const COLORS = {
    PM: COLORS_PM,
    AQI: COLORS_AQI,
    PDK: COLORS_PDK,
}

async function Download(dates) {
    const URL = `https://gis.krasn.ru/sc/api/1.0/projects/9/aggvalues?&key=654hblgm9gl8367h&time_begin=${dates[0]} 00:00:00&time_end=${dates[dates.length - 1]} 00:00:00&time_interval=1day&indicators=348,355&limit=30000'`;
    const responce = fetch(URL);
    const result = responce.then(e => XML2JSON(e));
    return result;
}

async function XML2JSON(e) {
    const data = e.text().then(e => {
        const result = [];
        const dom = new DOMParser();
        const xml = dom.parseFromString(e, "text/xml");
        const aggvalues = xml.getElementsByTagName("aggvalue");
        aggvalues.forEach((element) => {
            const indicator = element.getAttribute("indicator")
            const time = element.getAttribute("time").substring(0, element.getAttribute("time").length - 3).split(' ')[0];
            const value = Number((element.getElementsByTagName("avg")[0].innerHTML)).toFixed(4);
            const site = element.getAttribute("site");
            const coordinates = element.getElementsByTagName("location")[0];
            const x = coordinates.getAttribute("x");
            const y = coordinates.getAttribute("y");
            result.push({
                site: site, indicator: indicator, time: time, value: value, coordinates: proj4("EPSG:4326", "EPSG:3857", [
                    Number(x),
                    Number(y),
                ])
            })
        })
        return result;
    })
    return data;
}

async function CreateDates() {
    const today = new Date();
    const start = new Date();
    const dates = [];
    start.setDate(start.getDate() - 7)

    while (start.getTime() < today.getTime()) {
        dates.push(formatDate(start));
        start.setDate(start.getDate() + 1);
    }
    return dates;
}

function formatDate(date) {
    var dd = date.getDate();
    if (dd < 10) dd = "0" + dd;

    var mm = date.getMonth() + 1;
    if (mm < 10) mm = "0" + mm;

    var yy = date.getFullYear();
    if (yy < 10) yy = "0" + yy;

    return yy + "-" + mm + "-" + dd;
}

function DateToText(e) {
    if (e != undefined) {
        const date = e.split(' ')[0].split('-');
        const result = new Date(date[0], date[1] - 1, date[2], 0, 0, 0).toLocaleString('ru', {
            month: 'short',
            day: 'numeric',
            weekday: 'short',
        });
        return result
    }
    else {
        return new Date().toLocaleString('ru', {
            month: 'long',
            day: 'numeric',
            weekday: 'long',
            hour: 'numeric',
            minute: 'numeric',
        });
    }
}

function GetData(data, time, indicator) {
    const code = CODES[indicator];
    const result = data.filter(e => { if (e.time == time && e.indicator == code) return e });
    return result;
}

function GetDataAllTime(data, indicator) {
    const code = CODES[indicator];
    const result = data.filter(e => { if (e.indicator == code) return e });
    return result;
}

function CreateLayers(data, type) {
    const layers = data.map(e => {
        const value = (e.value * CONVERSION[type]).toFixed(1);
        for (let i = 1; i < RANGES[type].length; i++) {
            if (Number(value) >= RANGES[type][i - 1] && Number(value) < RANGES[type][i]) {
                return {
                    id: e.site,
                    fill: COLORS[type][i - 1].color,
                    radius: 25,
                    value: value,
                    text: COLORS[type][i - 1].text,
                    stroke: {
                        color: "white",
                        width: 2
                    },

                    zindex: 10,
                    coordinates: e.coordinates
                }
            }
        }
    })
    return layers;
}

function CreatePDK(data) {
    const CC = 35;
    const PDK = data.map(d => {
        const value = d.value * 1000;
        const PDK = (value / CC).toFixed(2);
        return {
            site: d.site, indicator: "400", time: d.time, value: PDK, coordinates: d.coordinates
        }
    });
    return PDK;
}

function CreateColoredTooltips(data, dates, type) {
    const avg = dates.map(d => {
        const timeData = GetData(data, d, type);
        const length = timeData.length;
        let amount = 0;
        timeData.forEach(t => {
            amount += Number(t.value);
        })
        return ((amount / length) * CONVERSION[type]).toFixed(1);
    });
    const coloredBox = avg.map(a => {
        for (let i = 0; i < RANGES[type].length; i++) {
            if (Number(a) >= RANGES[type][i - 1] && Number(a) < RANGES[type][i]) {
                return { color: COLORS[type][i - 1].color, text: COLORS[type][i - 1].text, value: a };
            }
        }
    })
    return coloredBox;
}

export { Download, CreateLayers, GetData, GetDataAllTime, CreateDates, CreateColoredTooltips, DateToText, CreatePDK }