<template>
  <div>
    <button
      type="button"
      class="btn-play"
      data-toggle="modal"
      data-target="#staticBackdrop"
    >
      <img src="../assets/wheel.svg" width="74px" height="74px" />
    </button>
    <div
      class="modal fade"
      id="staticBackdrop"
      aria-labelledby="staticBackdropLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="staticBackdropLabel">
              Выборите показатель
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col" v-for="i in indicators" :key="i.id">
                <button
                  class="btn btn-warning btn-lg"
                  @click="SelectIndicator(i.type)"
                  data-dismiss="modal"
                >
                  {{ i.name }}
                </button>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <div class="row full">
              <div class="col">
                <div class="form-check">
                  <input
                    type="checkbox"
                    class="form-check-input"
                    id="RememberSelection"
                    v-model="save"
                  />
                  <label
                    class="form-check-label font-weight-bold"
                    for="RememberSelection"
                    >Запомнить выбор</label
                  >
                </div>
              </div>
              <div class="col">
                <button
                  type="button"
                  class="btn btn-secondary"
                  data-dismiss="modal"
                >
                  Закрыть
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  data() {
    return {
      save: false,
      indicators: [
        {
          id: 0,
          name: "PM2.5",
          type: "PM",
        },
        {
          id: 1,
          name: "AQI",
          type: "AQI",
        },
        {
          id: 2,
          name: "ПДК",
          type: "PDK",
        },
      ],
    };
  },
  mounted() {
    const remember = localStorage.getItem("KNC_VIDEOCLIP_INDICATOR");
    if (!remember) $("#staticBackdrop").modal("show");
  },
  methods: {
    SelectIndicator(type) {
      if (this.save == true)
        localStorage.setItem("KNC_VIDEOCLIP_INDICATOR", type);
      this.$emit("update-indicator", type);
    },
  },
};
</script>

